@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/*
    DEMO STYLE
*/
body {
    font-family: "Poppins", sans-serif;
    background: #fafafa;
}

p {
    font-family: "Poppins", sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
}

.navbar {
    padding: 15px 10px;
    background: #fff;
    border: none;
    border-radius: 0;
    margin-bottom: 40px;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
    margin-top: 15px;
}

.navbar-btn {
    box-shadow: none;
    outline: none !important;
    border: none;
}
.user-notification {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1001;
    background: #ffeb3b;
    border: 1px solid #e3cf1f;
    text-align: center;
    box-shadow: 0px 0px 9px 0px rgba(148, 148, 148, 1);
    color: #c00000;
    padding: 4px 0;
}
.user-notification a {
    color: #007bffbd;
}
.line {
    width: 100%;
    height: 1px;
    border-bottom: 1px dashed #ddd;
    margin: 40px 0;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
    display: flex;
    width: 100%;
}

#sidebar {
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 999;
    background: #920101;
    color: #fff;
}

#sidebar.active {
    margin-left: -250px;
}

#sidebar .sidebar-header {
    padding: 40px 20px 20px 20px;
    background: #770101;
}

#sidebar ul.components {
    padding: 20px 0;
    border-bottom: 1px solid #920101;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}

#sidebar ul li a:hover {
    color: #770101;
    background: #fff;
}

#sidebar ul li.active > a,
a[aria-expanded="true"] {
    color: #fff;
    background: #770101;
}

a[data-toggle="collapse"] {
    position: relative;
}

.dropdown-toggle::after {
    display: block;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
}

ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #920101;
}

ul.CTAs {
    padding: 20px;
}

ul.CTAs a {
    text-align: center;
    font-size: 0.9em !important;
    display: block;
    border-radius: 5px;
    margin-bottom: 5px;
}

a.download {
    background: #fff;
    color: #7386d5;
}

a.article,
a.article:hover {
    background: #6d7fcc !important;
    color: #fff !important;
}

.github-btns .widget .btn {
    padding: 4px !important;
}
.github-btns .widget .social-count {
    padding: 4px !important;
}
/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
    width: calc(100% - 250px);
    padding: 40px;
    min-height: 100vh;
    transition: all 0.3s;
    position: absolute;
    top: 0;
    right: 0;
}

#content.active {
    width: 100%;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
    #sidebar {
        margin-left: -250px;
    }
    #sidebar.active {
        margin-left: 0;
    }
    #content {
        width: 100%;
    }
    #content.active {
        width: calc(100% - 250px);
    }
    #sidebarCollapse span {
        display: none;
    }
}

.separator {
    display: flex;
    align-items: center;
    text-align: center;
    color: #5d5d5d;
    font-weight: bold;
    margin: 35px 0;
}
.separator::before,
.separator::after {
    content: "";
    flex: 1 1;
    border-bottom: 3px solid #c6bebe;
}
.separator::before {
    margin-right: 0.25em;
}
.separator::after {
    margin-left: 0.25em;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.card-hover {
    opacity: 0.95;
}
.card-hover:hover {
    opacity: 1;
}
.card-hover:hover .card-body {
    background-color: #dddcdc;
}
.card-hover:hover .card {
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}
.otp-field {
    width: 38px;
    margin-right: 10px;
    padding-left: 12px;
    height: 40px;
}
.cursor-pointer {
    cursor: pointer;
}

.scroll-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
}
.scroll-container .indicator {
    height: 100%;
    background: #8b0000;
}

.scroll-to-top {
    position: fixed;
    bottom: 12px;
    left: 50%;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background: #8b0000;
    color: #fff;
    z-index: 1;
}
.scroll-to-top .icon {
    margin-top: 10px;
    -webkit-animation: scrollTop 0.5s alternate ease infinite;
            animation: scrollTop 0.5s alternate ease infinite;
}
@-webkit-keyframes scrollTop {
    from {
        transform: translateY(2px);
    }
    to {
        transform: translateY(-1px);
    }
}
@keyframes scrollTop {
    from {
        transform: translateY(2px);
    }
    to {
        transform: translateY(-1px);
    }
}

.custom-youtube-player {
    position: fixed;
    width: 450px;
    height: 300px;
    bottom: 40px;
    right: 40px;
    background: #000;
}
.custom-youtube-player .close,
.custom-youtube-player .handle {
    position: absolute;
    right: 0px;
    top: -30px;
    width: 32px;
    height: 32px;
    background: #000;
    color: #fff;
    opacity: 1;
    padding: 3px;
    cursor: pointer;
}
.custom-youtube-player .handle {
    right: 32px;
    padding-top: 8px;
    cursor: move;
}
.fp-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #f8f8f8ad;
}
.fp-container .fp-loader {
    top: 30%;
    left: 48%;
    z-index: 1000;
    position: absolute;
}

.battery-outer {
    height: 210px;
    width: 100px;
    border: 2px solid #222;
    padding: 4px;
    border-radius: 7px;
}
.battery-inner {
    width: 100%;
    margin: 2px 0;
    background-color: #01770a;
}
.battery-empty .battery-inner {
    background: #8b0000;
}
.battery-outer::before {
    content: "";
    width: 24px;
    position: absolute;
    top: -8px;
    height: 8px;
    background: #222;
    left: 49%;
    border-radius: 4px 4px 3px 3px;
}
.battery-outer .battery-charging-indicator {
    position: absolute;
    top: 40%;
    left: 49.2%;
    color: #cbd71d;
    font-size: 40px;
    display: none;
}
.battery-charging .battery-outer .battery-charging-indicator {
    display: block;
    -webkit-animation: fadeIn 0.8s infinite alternate;
            animation: fadeIn 0.8s infinite alternate;
}
@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.password-toogle-icon {
    position: absolute;
    top: 8px;
    right: 10px;
    z-index: 1000;
    cursor: pointer;
}

body.dark-mode {
    background: #1d2127;
    color: #fff;
}

body.dark-mode #sidebar {
    background: #1d2127;
}

body.dark-mode #sidebar .sidebar-header {
    background: #12171e;
}

body.dark-mode #sidebar ul li a {
    background: #12171e;
}

body.dark-mode .navbar {
    background: #dad3d3 !important;
    box-shadow: 1px 3px 0 1px #2f3139;
}

body.dark-mode .bg-light {
    background-color: #12171e !important;
}
body.dark-mode .table {
    color: #fff;
}
body.dark-mode #sidebar ul li a:hover {
    background: #131820;
    color: #ff9d9d;
}

body.dark-mode input {
    background: #12171e;
    color: #fff;
    border: 1px solid #454444;
}

body.dark-mode .page-link {
    background: transparent;
    color: #fff;
}

body.dark-mode .page-item.disabled .page-link {
    background: transparent;
}
body.dark-mode .list-group-item {
    background: #12171e;
    color: #fff;
}
body.dark-mode .list-group-item.active,
body.dark-mode .list-group-item:hover {
    background: #ababac;
}
body.dark-mode .card {
    background: #12171e;
}

.downloader {
  width: 500px;
  min-height: 128px;
  position: fixed;
  right: 18px;
  bottom: 18px;
  max-height: 700px;
  overflow-y: auto;
}
.downloader .card-header {
  color: #fff;
  background-color: rgb(93 11 11 / 92%);
}
.downloader .card .list-group {
  max-height: 300px;
  overflow: hidden;
  overflow-y: auto;
}

